import back from '@/components/back'

export default {
    components: {
		back
    },
    data(){
        return {
            id: this.$route.query.id,
            //info:null,
            list:null
        }
    },
    mounted(){
	    document.title = this.$route.meta.title
    },
    created() {
        this.pull()
    },
    methods: {
        pull() {
            //根据处方购买记录id获取处方单退款记录
            this.$get('/Prescription/Prescription/GetPrescriptionRefundRecordInfoByPreBuyRecordId', {
                PreBuyRecordId: this.id
            }).then(o => {
                //this.info = o
                this.list = o
            })
        },
        goDetail(info){
            this.$router.push(`/pre-detail?id=${info.PrescriptionId}`)
        }
    }
}



